.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

header a,
footer a {
  text-decoration: none; }

.c3, #header-menu ul li a:hover {
  background: purple; }

#header-menu {
  margin-top: 30px;
  margin-right: -7px; }
  #header-menu ul {
    padding: 0;
    margin: 0; }
    #header-menu ul li {
      display: inline-block;
      margin-right: -5px; }
      #header-menu ul li a {
        padding: 7.5px 15px;
        background: blue;
        color: white;
        margin: 0 7px;
        display: block; }

