.select {
  position: relative;
  display: block; }
  .select:before {
    content: "";
    border: 6px solid transparent;
    border-top-color: #676767;
    top: 50%;
    right: 10px;
    margin-top: -3px;
    pointer-events: none;
    position: absolute; }
  .select select {
    -webkit-appearance: none;
    -moz-appearance: none;
    height: 36px;
    width: 100%;
    padding: 0 10px;
    line-height: normal;
    border: 1px solid #ccc;
    background: #fff;
    display: block; }
  .select select::-ms-expand {
    display: none; }
  .select select:focus {
    border-color: #f7c723; }
  .select select:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 #000;
    border-color: #f7c723; }

